import React from 'react';
import './ButtonInfo.css';
import { Link } from 'react-router-dom';

function ButtonInfo(){
    return(
        <>
        <div className='inicio-info'>
            <p className='text-info'>Información DACE Monagas</p>
            <Link to='/InfoDace'>
                <button className='button-info'>
                        Información
                </button>
            </Link>
        </div>
        </>

    );
}

export { ButtonInfo };