import React from "react";
import './InicioSesion.css';
import { EncabezadoUdo } from "../EncabezadoUdo/EncabezadoUdo";
import { Navbar } from "../Navbar/Navbar";

function InicioSesion({
    setModalInicioSe, setCorreo, setUser , setPassword, user}){
   
    const onClick = () => {
        setModalInicioSe(false);
        setUser(""); 
        setCorreo("");
        setPassword("");
    }

    return(
        <>
        <div className="fondo-sesion sesion-activa">
            <EncabezadoUdo />

            <div className="datos-usuario">
                <p>Cédula: ****</p>
                <p>Nombre: ****</p>
                <p>Especialidad: ****</p>
            </div>

            <div className="navbar-info">

            <Navbar />
                <div className="pagina-principal">
                    <p>Su Email cargado y registrado es: <span>{user}</span></p>
                    <p>Para contactar el(la) Jefe de Departamento de su carrera: <span>rdiaz@gmail.com</span></p>

                    <p className='nota-arancel'>El arancel de Inscripción para <span>ALUMNOS REGULARES I-2023 (Octubre 2023 - Marzo 2024)</span> es de <span>Bs Digital 120,00</span> y el pago debe realizarse a la Cuenta Corriente del <span>Banco Mercantil</span> número <span>0105-0054-1810-5425-0324</span> a nombre de <span>UDO - Ingresos</span>. Para realizar transferencias pueden afiliar este número de cuenta y colocar el siguiente <span>R.I.F.: G-20000052-0</span> y colocar el correo electrónico <span>dacemonagas@gmail.com</span>.</p>
                    
                    <button className='cerrar-sesion' onClick={onClick}>
                        Cerrar sesión
                    </button>
                </div>
            </div>

        </div>
        </>
    );
}

export { InicioSesion }