import React from 'react';
import { useRef } from 'react';
import { gsap } from "gsap";
import { useGSAP } from '@gsap/react';
import './FormDace.css';
import { EncabezadoUdo } from '../EncabezadoUdo/EncabezadoUdo';
import { InicioSesion } from '../InicioSesion/InicioSesion';
import { ButtonIngreso} from './ButtonIngreso'
import { ButtonCrearClave } from './ButtonCrearClave';
import { Modal } from '../Modal/Modal';
import { Link } from 'react-router-dom';
import { ButtonRestaurarClave } from './ButtonRestaurarClave';

function FormDace(){
    const [correo, setCorreo] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [error, setError] = React.useState(false);

    const [modalnicioSe, setModalInicioSe] = React.useState(false);

    const [user, setUser] = React.useState([]);

    const containerSesion = useRef();
    const tl = useRef();
    useGSAP(
    () => {
        tl.current = gsap
        .timeline()
        .from(".encabezado", { duration: 3, y: 30 })
        .from(".formulario", { duration: 3, y: 30 }, '<')
        .from(".regresar-panta", { duration: 3, y: 30 }, '<')
    },
    [],
    containerSesion, 
    );

    const onSubmit = async (event) =>{
        event.preventDefault();

        if((correo === "" || password === "") || (correo ==! event.target.type) ){
            setError(true);
            setModalInicioSe(false);
            return
        } 
        // else if (correo ==! event.target.type) {
        //     setModalInicioSe(false);
        //     return
        // }
            
        setModalInicioSe(true);
        setError(false);
        setUser([correo]);
    }
    
    const onChangeN = (event) =>{
        setCorreo(event.target.value);
    }
    const onChangeP = (event) =>{
        setPassword(event.target.value);
    }

    return(
        <>
            <div className='fondo form'   ref={containerSesion}>
                <EncabezadoUdo />

                <form onSubmit={onSubmit} className='formulario' >
                    <h3 className='subtitulo'>IntraDACE - Estudiantes</h3>

                    <div className='preguntas'>
                        <label htmlFor="correo" className='descripcion' >Ingrese su Correo: </label>
                        <input 
                            id='correo'  
                            name='correo'
                            type='email' 
                            value={correo} 
                            onChange={onChangeN} 
                            className='respuesta' 
                            placeholder='example@gmail.com' />

                        <label htmlFor="password" className='descripcion' >Ingrese su Contraseña: </label>
                        <input 
                            id='password'  
                            name='password'
                            type='password' 
                            value={password} 
                            onChange={onChangeP} 
                            className='respuesta' />

                        {error && <p className='alert'>Todos los campos son obligatorios</p>}
                        <ButtonIngreso  />
                        
                        {modalnicioSe && (
                            <Modal>
                                <InicioSesion user={user} setCorreo={setCorreo} setPassword={setPassword} setModalInicioSe={setModalInicioSe} setUser={setUser} />
                            </Modal>
                        )}

                        {/* <p className='nota'>Nota: Su usuario será la letra "m" seguido de su número de cédula</p> */}
                    </div>
            
                    {/* <div className='botones'>
                        <ButtonCrearClave />    
                        <ButtonRestaurarClave />
                    </div> */}
                </form>

                <Link to={'/'}>
                    <button className='regresar-panta'>
                            Regresar
                    </button>
                </Link>
            </div>
        </>
    );
}

export { FormDace };

