import React from "react";
import './Navbar.css'
import { ButtonInfoInscrip } from "./ButtonInfoInscrip";
import { InfoInscrip } from "./InfoInscrip";
import { ButtonDocum } from "./ButtonDocum";
import { InfoDocuments } from "./InfoDocuments";
import { ButtonTrabArea } from "./ButtonTrabArea";
import { InfoTrabArea } from "./InfoTrabArea";
import { ButtonAspirante } from "./ButtonAspirante";
import { InfoAspirante } from "./InfoAspirante";
import { ButtonActualizar } from "./ButtonActualizar";
import { InfoActualizar } from "./InfoActualizar";

function Navbar() {
    const [state, setState] = React.useState(false);
    const [stateDoc, setStateDoc] = React.useState(false);
    const [stateTA, setStateTA] = React.useState(false);
    const [stateAsp, setStateAsp] = React.useState(false);
    const [stateAct, setStateAct] = React.useState(false);

    return(
        <>
        <nav className="navbar">
            <ButtonInfoInscrip setState={setState} />
            {state && <InfoInscrip /> }

            <ButtonDocum setStateDoc={setStateDoc} />
            {stateDoc && <InfoDocuments />}
            
            <ButtonTrabArea setStateTA={setStateTA}/>
            {stateTA && <InfoTrabArea />}

            <ButtonAspirante setStateAsp={setStateAsp} />
            {stateAsp && <InfoAspirante />}

            <ButtonActualizar setStateAct={setStateAct} />
            {stateAct && <InfoActualizar /> }
        </nav>
        </>
    );
}

export { Navbar };