import React from "react";
import { Modal } from "../Modal/Modal";
import {SolicitarAreas} from '../InfoTrabArea/SolicitarAreas';
import {SolicitarTrabGrado} from '../InfoTrabArea/SolicitarTrabGrado';

function InfoTrabArea(){
    const [solAreas, setSolAreas] = React.useState(false);
    const [solTrabG, setSolTrabG] = React.useState(false);

    const solicitudAreas = () =>{
        setSolAreas((state => !state));
    }
    const solicitudTrabaGrad = () =>{
        setSolTrabG((state => !state));
    }

    return(
        <ul className="inscripcion">
            <li>
                <button onClick={solicitudAreas}>Solicitar Áreas de Grado</button>
                {solAreas && (
                    <Modal>
                        <SolicitarAreas setSolAreas={setSolAreas} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudTrabaGrad}>Solicitar Trabajo de Grado</button>
                {solTrabG && (
                    <Modal>
                        <SolicitarTrabGrado setSolTrabG={setSolTrabG} />
                    </Modal>
                )} 
            </li>
            <p>Solicitud activa hasta el 06/10/2023</p>
        </ul>
    );
}

export { InfoTrabArea };