import React from "react";
// import './CargarArancel.css';

function ActuaDatos({setActDatos}){
    const cerrar = () =>{
        setActDatos(false)
    }

    return(
        <>
            <div className="item">
                <h1>Hola</h1>


                <button onClick={cerrar} className="cerrar-sesion">Regresar</button>
            </div>
        </>
    );
}

export { ActuaDatos };