import React from "react";
import { CargarAracel } from '../InfoInscrip/CargarAracel';
import { HorariosAsignaturas } from '../InfoInscrip/HorariosAsignaturas';
import { VerAsignaturasEmail } from '../InfoInscrip/VerAsignaturasEmail';
import { CitasInscripcion } from '../InfoInscrip/CitasInscripcion';
import { SolicitarApelacion } from '../InfoInscrip/SolicitarApelacion';
import { Modal } from "../Modal/Modal";

function InfoInscrip(){
    const [carAran, setCarAran] = React.useState(false);
    const [horaAsig, setHoraAsig] = React.useState(false);
    const [asigYEmail, setAsigYEmail] = React.useState(false);
    const [citaInscrip, setCitaInscrip] = React.useState(false);
    const [apelacion, setApelacion] = React.useState(false);

    const cargarArancel = () =>{
        setCarAran((state => !state));
    }
    const verHorarioAsignatura = () =>{
        setHoraAsig((state => !state));
    }
    const verAsignaturaYEmail = () =>{
        setAsigYEmail((state => !state));
    }
    const citaInscripcion = () =>{
        setCitaInscrip((state => !state));
    }
    const solicApelacion = () =>{
        setApelacion((state => !state));
    }

    return(
        <ul className="inscripcion">
            <li>
                <button onClick={cargarArancel}>Cargar Arancel Incripción I-2023</button>
                {carAran && (
                    <Modal>
                        <CargarAracel setCarAran={setCarAran} />
                    </Modal>
                )}             
            </li>
            <li>
                <button onClick={verHorarioAsignatura}>Ver Horarios Asignaturas por Cursar</button>
                {horaAsig && (
                    <Modal>
                        <HorariosAsignaturas setHoraAsig={setHoraAsig} />
                    </Modal>
                )}             
            </li>
            <li>
                <button onClick={verAsignaturaYEmail}>Ver Asignaturas por Cursar y Email del Profesor</button>
                {asigYEmail && (
                    <Modal>
                        <VerAsignaturasEmail setAsigYEmail={setAsigYEmail} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={citaInscripcion}>Ver Citas de Incripción I-2023</button>
                {citaInscrip && (
                    <Modal>
                         <CitasInscripcion setCitaInscrip={setCitaInscrip} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicApelacion}>Solicitar apelación</button>
                {apelacion && (
                    <Modal>
                        <SolicitarApelacion setApelacion={setApelacion} />
                    </Modal>
                )}  
            </li>
    </ul>
    );
}

export { InfoInscrip };