import React from "react";
import './InfoDace.css';
import { useRef } from 'react';
import { gsap } from "gsap";
import { useGSAP } from '@gsap/react';

import { EncabezadoUdo } from "../EncabezadoUdo/EncabezadoUdo";
import { Link } from "react-router-dom";

function InfoDace(){
    const container = useRef();
    const tl = useRef();
    
    useGSAP(
    () => {
        tl.current = gsap
        .timeline()
        .from(".informacion", { duration: 4, y: 30 })
        .from(".efecto-button", { duration: 4, y: 30 }, '<')
        .from(".encabezado", { duration: 4, y: 30 }, '<')
    },
    [],
    container, 
    );

    return(
        <>
            <div className="info-dace fondo-info"  ref={container}>
                <EncabezadoUdo />
                
                <div className='informacion'>
                    <h3 className='subtitulo'>Aviso e información</h3>
                    <p className="info-semestre">ACTIVADO ENLACE DE REGISTRO DE RECIBO DE INSCRIPCIÓN CORRESPONDIENTE AL PERÍODO ACADÉMICO I-2023.</p>
                    <p className='nota-arancel'>El arancel de Inscripción para <span>ALUMNOS REGULARES I-2023 (Octubre 2023 - Marzo 2024)</span> es de <span>Bs Digital 120,00</span> y el pago debe realizarse a la Cuenta Corriente del <span>Banco Mercantil</span> número <span>0105-0054-1810-5425-0324</span> a nombre de <span>UDO - Ingresos</span>. Para realizar transferencias pueden afiliar este número de cuenta y colocar el siguiente <span>R.I.F.: G-20000052-0</span> y colocar el correo electrónico <span>dacemonagas@gmail.com</span>.</p>

                    <div className="buttons-info">
                        <button className="button-dace"><a href="http://170.254.173.145/">Ir a DACE - Monagas</a></button>
                        <button className="button-egresado"><a href="http://201.249.180.234/egresados/">Ir a UDO - Egresados</a></button>
                        <button className="button-arancel"><a href="http://170.254.173.145/ArancelesRegulares.php">Ver Aranceles Estudiantiles</a></button>
                        <button className="button-censo"><a href="https://docs.google.com/forms/d/e/1FAIpQLScFe1EOdYnvU9ih1b8fooW7awt4bE9xiVRPPHQJs8VmOf3nYw/viewform?pli=1" target="_blank">Censo de Bienestar Estudiantil</a></button>
                        <button className="button-resolucion"><a href="http://170.254.173.145/Formas/CU_070_2021.jpg">Resolución CU 070/2021</a></button>
                    </div>
                </div>

                    <Link to={'/'}>
                        <button className='regresar efecto-button'>
                                Regresar
                        </button>
                    </Link>
            </div>
        </>
    );
}

export { InfoDace };

