import React from "react";
import { Modal } from "../Modal/Modal";
import { CargaAcademica } from '../InfoDocuments/CargaAcademica';
import { CargaAyEmailP } from '../InfoDocuments/CargaAyEmailP';
import { ConstanciaEst } from '../InfoDocuments/ConstanciaEst';
import { ConteoEstad } from '../InfoDocuments/ConteoEstad';
import { Horario } from '../InfoDocuments/Horario';
import { ImprimPlaniSolv } from '../InfoDocuments/ImprimPlaniSolv';
import { MateriasAprob } from '../InfoDocuments/MateriasAprob';
import { NotasSemesAnt } from '../InfoDocuments/NotasSemesAnt';
import { RecordNotApro } from '../InfoDocuments/RecordNotApro';
import { RegistGeneAsig } from '../InfoDocuments/RegistGeneAsig';

function InfoDocuments(){
    const [constEst, setConstEst] = React.useState(false);
    const [cargAcad, setCargAcad] = React.useState(false);
    const [carAcadyEmail, setCarAcadyEmail] = React.useState(false);
    const [horarios, setHorarios] = React.useState(false);
    const [notSemAnt, setNotSemAnt] = React.useState(false);
    const [recNotApro, setRecNotApro] = React.useState(false);
    const [materApro, setMaterApro] = React.useState(false);
    const [regGenAsig, setRegGenAsig] = React.useState(false);
    const [contEstadis, setContEstadis] = React.useState(false);
    const [impPlaniSolv, setImpPlaniSolv] = React.useState(false);

    const solicitudConstancia = () =>{
        setConstEst((state => !state));   
    }
    const solicitudCargaAcadem = () =>{
        setCargAcad((state => !state));
    }
    const solicitudCargaAcadeyEmail = () =>{
        setCarAcadyEmail((state => !state)); 
        }
    const solicitudHorarios = () =>{
        setHorarios((state => !state));
  
    }
    const solicitudNotasSemesAnte = () =>{
        setNotSemAnt((state => !state)); 
    }
    const solicitudRecorNotApr = () =>{
        setRecNotApro((state => !state)); 
    }
    const solicitudMatePorApro = () =>{
        setMaterApro((state => !state));  
    }
    const solicitudRegiGeneAsig = () =>{
        setRegGenAsig((state => !state));
    }
    const solicitudConteoEstadi = () =>{
        setContEstadis((state => !state));
    }
    const solicitudPlaniSolven = () =>{
        setImpPlaniSolv((state => !state));  
    }

    return(
        <ul className="inscripcion">
            <li>
                <button onClick={solicitudConstancia}>Solicitar Constancia de Estudio</button>
                {constEst && (
                    <Modal>
                        <ConstanciaEst setConstEst={setConstEst} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudCargaAcadem}>Ver Carga Académica Semestre</button>
                {cargAcad && (
                    <Modal>
                        <CargaAcademica setCargAcad={setCargAcad} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudCargaAcadeyEmail}>Ver Carga Académica Y Email Del Profesor</button>
                {carAcadyEmail && (
                    <Modal>
                        <CargaAyEmailP setCarAcadyEmail={setCarAcadyEmail} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudHorarios}>Ver Horario</button>
                {horarios && (
                    <Modal>
                        <Horario setHorarios={setHorarios} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudNotasSemesAnte}>Ver Notas Semestre Anterior</button>
                {notSemAnt && (
                    <Modal>
                        <NotasSemesAnt setNotSemAnt={setNotSemAnt} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudRecorNotApr}>Ver Record Notas Aprobadas</button>
                {recNotApro && (
                    <Modal>
                        <RecordNotApro setRecNotApro={setRecNotApro} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudMatePorApro}>Ver Materias por Aprobar</button>
                {materApro && (
                    <Modal>
                        <MateriasAprob setMaterApro={setMaterApro} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudRegiGeneAsig}>Ver Registro General de Asignaturas</button>
                {regGenAsig && (
                    <Modal>
                        <RegistGeneAsig setRegGenAsig={setRegGenAsig} />
                    </Modal>
                )}  
            </li>
            <li>
                <button onClick={solicitudConteoEstadi}>Ver Conteo Estadístico</button>
                {contEstadis && (
                    <Modal>
                        <ConteoEstad setContEstadis={setContEstadis} />
                    </Modal>
                )}  
            </li>
            <li>
                <button><a href="https://docs.google.com/forms/d/e/1FAIpQLScFe1EOdYnvU9ih1b8fooW7awt4bE9xiVRPPHQJs8VmOf3nYw/viewform?pli=1" target="_blank">Censo de Bienestar Estudiantil</a></button>
            </li>
            <li>
                <button onClick={solicitudPlaniSolven}>Imprimir Planilla de Solvencia</button>
                {impPlaniSolv && (
                    <Modal>
                        <ImprimPlaniSolv setImpPlaniSolv={setImpPlaniSolv} />
                    </Modal>
                )}  
            </li>
        </ul>
    );
}

export { InfoDocuments };