import React from "react";
import { Modal } from "../Modal/Modal";
import { ConsignarDocu } from "../InfoAspirante/ConsignarDocu";
import { CronogramaA } from "../InfoAspirante/CronogramaA";
import { PlaniSoliGrado } from "../InfoAspirante/PlaniSoliGrado";
import { RecomSolGrado } from "../InfoAspirante/RecomSolGrado";
import { RequisiGrado } from "../InfoAspirante/RequisiGrado";
import { SolvenTitulo } from "../InfoAspirante/SolvenTitulo";
import { ConteoEstad } from "../InfoDocuments/ConteoEstad";

function InfoAspirante(){
    const [crongArea, setCrongArea] = React.useState(false);
    const [recSoliGrado, setRecSoliGrado] = React.useState(false);
    const [requiGrado, setRequiGrado] = React.useState(false);
    const [contEstadis, setContEstadis] = React.useState(false);
    const [consigDocum, setConsigDocum] = React.useState(false);
    const [planiSolGrad, setPlaniSolGrad] = React.useState(false);
    const [solvTitulo, setSolvTitulo] = React.useState(false);

    const solicitudCronog = () =>{
        setCrongArea((state => !state));   
    }
    const solicitudRecomenSolGrad = () =>{
        setRecSoliGrado((state => !state));
    }
    const solicitudRequisGrado = () =>{
        setRequiGrado((state => !state)); 
    }
    const solicitudConteoEstadi = () =>{
        setContEstadis((state => !state));
    }
    const solicitudConsignarDoc = () =>{
        setConsigDocum((state => !state)); 
    }
    const solicitudPlaniGrado = () =>{
        setPlaniSolGrad((state => !state)); 
    }
    const solicitudSolvenciaTitulo = () =>{
        setSolvTitulo((state => !state));
    }

    return(
        <ul className="inscripcion">
            <li>
                <button onClick={solicitudCronog}>Cronograma de Áreas de Grado</button>
                {crongArea && (
                    <Modal>
                        <CronogramaA setCrongArea={setCrongArea} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudRecomenSolGrad}>Recomendaciones para Solicitar Grado</button>
                {recSoliGrado && (
                    <Modal>
                        <RecomSolGrado setRecSoliGrado={setRecSoliGrado} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudRequisGrado}>Requisitos de Grado</button>
                {requiGrado && (
                    <Modal>
                        <RequisiGrado setRequiGrado={setRequiGrado} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudConteoEstadi}>Ver e Imprimir Conteo Estadístico</button>
                {contEstadis && (
                    <Modal>
                        <ConteoEstad setContEstadis={setContEstadis} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudConsignarDoc}>Consignar Documentos para Grado</button>
                {consigDocum && (
                    <Modal>
                        <ConsignarDocu setConsigDocum={setConsigDocum} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudPlaniGrado}>Planilla Solicitud de Grado</button>
                {planiSolGrad && (
                    <Modal>
                        <PlaniSoliGrado setPlaniSolGrad={setPlaniSolGrad} />
                    </Modal>
                )} 
            </li>
            <li>
                <button onClick={solicitudSolvenciaTitulo}>Solvencia para Título</button>
                {solvTitulo && (
                    <Modal>
                        <SolvenTitulo setSolvTitulo={setSolvTitulo} />
                    </Modal>
                )} 
            </li>   
         </ul>
    );
}

export { InfoAspirante };