import React from "react";
import "./ReciboArancel.css"

function ReciboArancel({monto, banco, referencia, fecha, setCarAran}){
    const cerrar = () =>{
        setCarAran(false)
    }
    return(
        <>
            <div className="item">
                <div className="contenedor-recibo">
                    <h3 className="subtitulo">Arancel cargado exitosamente</h3>
                    
                    <div className="recibo">
                        <p className="label-recibo">Número de referencia: <span>{referencia}</span> </p>
                        <p className="label-recibo">Transferencia desde el banco: <span>{banco}</span></p>
                        <p className="label-recibo">Fecha: <span>{fecha}</span> </p>
                        <p className="label-recibo">Monto de la transferencia: <span>{monto}</span> </p>
                    </div>

                    <button onClick={cerrar} className="cerrar-sesion">Regresar</button>
                </div>
            </div>
        </>
    );
}

export { ReciboArancel };