import React from "react";
// import './CargarArancel.css';

function ConteoEstad({setContEstadis}){
    const cerrar = () =>{
        setContEstadis(false);
    }

    return(
        <>
            <div className="item">
                <div className="contenedor-mensaje">
                    <h2 className="subtitulo">Conteo Estadístico</h2>
                    
                    <div className="mensaje">
                        <p className="nota">Estimado usuario:</p>
                        <p className="nota">En estos momentos no podemos procesar su solicitud.</p>
                        <p className="nota">Agradecemos mantenerse informado a través de nuestros portales.</p>
                    </div>

                    <button onClick={cerrar} className="cerrar-sesion">Regresar</button>
                </div>
            </div>
        </>
    );
}

export { ConteoEstad };