import React from "react";
import './CargarArancel.css';
import { Modal } from "../Modal/Modal";
import { ReciboArancel } from "./ReciboArancel";

function CargarAracel({setCarAran}){
    const [referencia, setReferencia] = React.useState("");
    const [banco, setBanco] = React.useState("");
    const [fecha, setFecha] = React.useState("");
    const [monto, setMonto] = React.useState("");
    const [mostrar, setMostrar] = React.useState(false);
    // const [error, setError] = React.useState(false);

    
    const onSubmit = (event) =>{
        event.preventDefault();

        if(banco === "" || fecha === "" || monto === "" || referencia === ""){
            // setError(true);
            setMostrar(false);
            return
        }
        setMostrar(true);

        // setError(false);
    }

    const onChangeR = (event) =>{
        setReferencia(event.target.value);
    }
    const onChangeB = (event) =>{
        setBanco(event.target.value);
    }
    const onChangeF = (event) =>{
        setFecha(event.target.value);
    }
    const onChangeM = (event) =>{
        setMonto(event.target.value);
    }

    console.log(referencia, banco, fecha, monto);
    // console.log(banco);

    const cerrar = () =>{
        setCarAran(false)
    }

    return(
        <>
            <div className="item">
                <form className="formulario-arancel" onSubmit={onSubmit}>
                    <div className="boton-y-titulo">
                        <button onClick={cerrar} className="atras">Atras</button>
                        <h2 className="subtitulo">Carga de Arancel</h2>
                    </div>
                    
                    <div className="preguntas-formu">
                        <label htmlFor="referencia" className='descripcion' >Numero de referencia: </label>
                        <input 
                            id='referencia'  
                            name='referencia'
                            type='text' 
                            value={referencia} 
                            onChange={onChangeR} 
                            className='respuesta-formu' 
                            required
                            placeholder='12345678' />
                        <p className="nota">No debe incluir los primeros "00"</p>

                        <label htmlFor="banco" className='descripcion' >Transferencia desde el banco: </label>
                        <select 
                            id="banco" 
                            name="banco"
                            value={banco} 
                            onChange={onChangeB}
                            defaultValue={""}
                            required
                            className='respuesta-formu pointer' >
                            <option value="" disabled>Seleccione un banco</option>
                            <option value="Banesco">Banesco</option>
                            <option value="Venezuela">Venezuela</option>
                            <option value="Mercantil">Mercantil</option>
                            <option value="BancaAmiga">BancaAmiga</option>
                        </select>

                        <label htmlFor="fecha" className='descripcion' >Fecha: </label>
                        <input 
                            id="fecha" name="banco" type="date"
                            value={fecha}
                            onChange={onChangeF} 
                            required
                            className='respuesta-formu pointer'></input>

                        <label htmlFor="monto" className='descripcion' >Monto de la transferencia: </label>
                        <select 
                            id="monto" 
                            name="monto" 
                            value={monto} 
                            onChange={onChangeM} 
                            defaultValue={""}
                            required
                            className='respuesta-formu pointer'>
                            <option value="" disabled>Seleccione un monto</option>
                            <option value="90,00">90,00 Bs. D.</option>
                            <option value="120,00">120,00 Bs. D.</option>
                            <option value="150,00">150,00 Bs. D.</option>
                            <option value="200,00">200,00 Bs. D.</option>
                        </select>

                        <p className="nota">Verifique los datos antes de guardar. No se podra modificar una vez guardados</p>

                        <button 
                            className="button-guardar">Guardar</button>
                    </div>

                    {mostrar && (
                        <Modal>
                            <ReciboArancel monto={monto} referencia={referencia} fecha={fecha} banco={banco} setCarAran={setCarAran} />
                        </Modal>
                    )}
                </form>
            </div>
        </>
    );
}

export { CargarAracel };