import React from "react";
import { Modal } from "../Modal/Modal";
import {ActuaDatos} from '../InfoActualizar/ActuaDatos';
import {UsuarioClave} from '../InfoActualizar/UsuarioClave';
import {CambioClave} from '../InfoActualizar/CambioClave';

function InfoActualizar(){
    const [actDatos, setActDatos] = React.useState(false);
    const [usuClav, setUsuClav] = React.useState(false);
    const [cambClave, setCambClave] = React.useState(false);

    const actualizarDatos = () =>{
        setActDatos((state => !state));
    }
    const verUsuarioClave = () =>{
        setUsuClav((state => !state));
    }
    const cambiarClave = () =>{
        setCambClave((state => !state));
        }

    return(
        <ul className="inscripcion">
            <li>
                <button onClick={actualizarDatos}>Actualizar Datos</button>
                {actDatos && (
                    <Modal>
                        <ActuaDatos setActDatos={setActDatos} />
                    </Modal>
                )}
            </li>
            <li>
                <button onClick={verUsuarioClave}>Ver Usuario y Clave de Correo</button>
                {usuClav && (
                    <Modal>
                        <UsuarioClave setUsuClav={setUsuClav} />
                    </Modal>
                )}
            </li>
            <li>
                <button onClick={cambiarClave}>Cambiar Clave</button>
                {cambClave && (
                    <Modal>
                        <CambioClave setCambClave={setCambClave} />
                    </Modal>
                )}
            </li>
        </ul>
    );
}

export { InfoActualizar };